<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <line :x1="circleConfig.x1"
                    :y1="circleConfig.y1"
                    :x2="circleConfig.x2"
                    :y2="circleConfig.y2"
                    :stroke="circleConfig.stroke"
                    :stroke-width="circleConfig.strokeWidth"></line>
          </svg>
        </div>
        <div class="svg-config-form__wrapper">
          <el-form ref="circleConfigForm" :model="circleConfig" label-width="200px">
            <el-form-item label="坐标(x1)">
              <el-slider
                  v-model="circleConfig.x1"
                  :min="50"
                  :max="300"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="坐标(y1)">
              <el-slider
                  v-model="circleConfig.y1"
                  :min="50"
                  :max="300"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="坐标(x2)">
              <el-slider
                  v-model="circleConfig.x2"
                  :min="50"
                  :max="300"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="坐标(y2)">
              <el-slider
                  v-model="circleConfig.y2"
                  :min="50"
                  :max="300"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="笔划颜色(stroke)">
              <el-color-picker v-model="circleConfig.stroke" show-alpha >
              </el-color-picker>
              {{circleConfig.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth)">
              <el-slider
                  v-model="circleConfig.strokeWidth"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import DemoPageMixin from '@/mixins/demo-page-mixin'
export default {
  name: 'SVGDemo004',
  mixins: [DemoPageMixin],
  data () {
    return {
      circleConfig: {
        x1: 40,
        y1: 50,
        x2: 140,
        y2: 150,
        stroke: 'rgba(0, 0, 0, 0.8)',
        strokeWidth: 2
      }
    }
  }
}
</script>

<style scoped>

</style>